	module.exports = {"data":{"challengeNode":{"title":"Problem 234: Semidivisible numbers","description":["For an integer n ≥ 4, we define the lower prime square root of n, denoted by lps(n), as the largest prime ≤ √n and the upper prime square root of n, ups(n), as the smallest prime ≥ √n.","So, for example, lps(4) = 2 = ups(4), lps(1000) = 31, ups(1000) = 37.","Let us call an integer n ≥ 4 semidivisible, if one of lps(n) and ups(n) divides n, but not both.","","The sum of the semidivisible numbers not exceeding 15 is 30, the numbers are 8, 10 and 12. 15 is not semidivisible because it is a multiple of both lps(15) = 3 and ups(15) = 5.","As a further example, the sum of the 92 semidivisible numbers up to 1000 is 34825.","","What is the sum of all semidivisible numbers not exceeding 999966663333 ?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-234-semidivisible-numbers","blockName":"Project Euler","tests":[{"text":"<code>euler234()</code> should return 1259187438574927000.","testString":"assert.strictEqual(euler234(), 1259187438574927000, '<code>euler234()</code> should return 1259187438574927000.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler234() {\n  // Good luck!\n  return true;\n}\n\neuler234();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-235-an-arithmetic-geometric-sequence","id":"5900f4571000cf542c50ff69"},"slug":"/coding-interview-prep/project-euler/problem-234-semidivisible-numbers"}}

